.Shelve {
  &-container{
    display: flex;
    flex-direction: column;
  }

  &-header {
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
  }
}